"use client"

import * as React from "react"
import {
  BookOpen, Bot, ChevronRight, Coins, GraduationCap, Home, LogOut,
  School, Sparkles, CreditCard, Users, User, Settings
} from "lucide-react"
import Link from "next/link"
import { usePathname, useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"

import {
  Sidebar, SidebarContent, SidebarFooter, SidebarHeader, SidebarMenu,
  SidebarMenuButton, SidebarMenuItem, SidebarMenuSub, SidebarMenuSubButton,
  SidebarMenuSubItem, SidebarRail, SidebarGroup, SidebarGroupLabel, useSidebar,
} from "@/components/ui/sidebar"
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from "@/components/ui/collapsible"
import {
  DropdownMenu, DropdownMenuContent, DropdownMenuGroup, DropdownMenuItem,
  DropdownMenuLabel, DropdownMenuSeparator, DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"

function SidebarContent_() {
  const pathname = usePathname()
  const router = useRouter()
  const [user, setUser] = React.useState<any>(null)
  const [credits, setCredits] = React.useState<number | null>(null)
  const { setOpen } = useSidebar()
  const hoverTimeoutRef = React.useRef<NodeJS.Timeout | null>(null)

  // 
  const handleLogout = async () => {
    const supabase = createClient()
    if (supabase) await supabase.auth.signOut()
    localStorage.removeItem('currentUser')
    router.replace('/login')
  }

  React.useEffect(() => {
    // 1.  Authing 
    const localUserStr = localStorage.getItem('currentUser')
    if (localUserStr) {
      try {
        const localUser = JSON.parse(localUserStr)
        setUser(localUser)
        
        //  ID Supabase 
        const userId = localUser.id || localUser.sub
        if (userId) {
          const supabase = createClient()
          if (supabase) {
             supabase
              .from("user_credits")
              .select("credits")
              .eq("user_id", userId)
              .single()
              .then(({ data }) => {
                 if (data) setCredits(data.credits)
              })
          }
        }
      } catch (e) {
        console.error("", e)
      }
    }
  }, [])

  // 
  React.useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      if (e.clientX < 50) {
        if (hoverTimeoutRef.current) clearTimeout(hoverTimeoutRef.current)
        setOpen(true)
      } else if (e.clientX > 280) {
        if (hoverTimeoutRef.current) clearTimeout(hoverTimeoutRef.current)
        hoverTimeoutRef.current = setTimeout(() => { setOpen(false) }, 300)
      }
    }
    window.addEventListener("mousemove", handleMouseMove)
    return () => {
      window.removeEventListener("mousemove", handleMouseMove)
      if (hoverTimeoutRef.current) clearTimeout(hoverTimeoutRef.current)
    }
  }, [setOpen])

  // 
  const handleAnchorClick = (e: React.MouseEvent<HTMLAnchorElement>, href: string) => {
    if (href.startsWith("/#") && pathname === "/") {
      e.preventDefault()
      const sectionId = href.substring(2)
      const element = document.getElementById(sectionId)
      if (element) element.scrollIntoView({ behavior: "smooth", block: "start" })
    }
  }

  return (
    <>
      <SidebarHeader>
        <SidebarMenu>
          <SidebarMenuItem>
            <SidebarMenuButton size="lg" asChild>
              <Link href="/">
                <div className="flex aspect-square size-8 items-center justify-center rounded-lg bg-primary text-primary-foreground">
                  <School className="size-4" />
                </div>
                <div className="grid flex-1 text-left text-sm leading-tight">
                  <span className="truncate font-semibold"></span>
                  <span className="truncate text-xs">AI</span>
                </div>
              </Link>
            </SidebarMenuButton>
          </SidebarMenuItem>
        </SidebarMenu>
      </SidebarHeader>
      
      <SidebarContent>
        <SidebarGroup>
          <SidebarGroupLabel></SidebarGroupLabel>
          <SidebarMenu>
            <SidebarMenuItem><SidebarMenuButton asChild tooltip="" isActive={pathname === "/"}><Link href="/"><Home /><span></span></Link></SidebarMenuButton></SidebarMenuItem>
            <SidebarMenuItem><SidebarMenuButton asChild tooltip="" isActive={pathname === "/chat"}><Link href="/chat"><Bot /><span></span></Link></SidebarMenuButton></SidebarMenuItem>
          </SidebarMenu>
        </SidebarGroup>
        
        <SidebarGroup>
          <SidebarGroupLabel></SidebarGroupLabel>
          <SidebarMenu>
             <SidebarMenuItem><SidebarMenuButton asChild><Link href="/teacher"><Users /><span></span></Link></SidebarMenuButton></SidebarMenuItem>
             <SidebarMenuItem><SidebarMenuButton asChild><Link href="/parent"><User /><span></span></Link></SidebarMenuButton></SidebarMenuItem>
             <SidebarMenuItem><SidebarMenuButton asChild><a href="/#pricing" onClick={(e) => handleAnchorClick(e, "/#pricing")}><CreditCard /><span></span></a></SidebarMenuButton></SidebarMenuItem>
          </SidebarMenu>
        </SidebarGroup>
      </SidebarContent>

      <SidebarFooter>
        <SidebarMenu>
          {user ? (
            <SidebarMenuItem>
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <SidebarMenuButton size="lg" className="data-[state=open]:bg-sidebar-accent data-[state=open]:text-sidebar-accent-foreground">
                    <Avatar className="h-8 w-8 rounded-lg">
                      <AvatarImage src={user.photo || user.user_metadata?.avatar_url || "/placeholder.svg"} alt={user.email} />
                      <AvatarFallback className="rounded-lg">{user.email ? user.email.substring(0, 2).toUpperCase() : "ME"}</AvatarFallback>
                    </Avatar>
                    <div className="grid flex-1 text-left text-sm leading-tight">
                      <span className="truncate font-semibold">{user.nickname || user.username || user.email?.split("@")[0] || ""}</span>
                      <span className="truncate text-xs">{user.email}</span>
                    </div>
                    <ChevronRight className="ml-auto size-4 rotate-90" />
                  </SidebarMenuButton>
                </DropdownMenuTrigger>
                <DropdownMenuContent className="w-(--radix-dropdown-menu-trigger-width) min-w-56 rounded-lg" side="bottom" align="end" sideOffset={4}>
                  <DropdownMenuLabel className="p-0 font-normal">
                    <div className="flex items-center gap-2 px-1 py-1.5 text-left text-sm">
                      <Avatar className="h-8 w-8 rounded-lg">
                        <AvatarImage src={user.photo || user.user_metadata?.avatar_url} alt={user.email} />
                        <AvatarFallback className="rounded-lg">ME</AvatarFallback>
                      </Avatar>
                      <div className="grid flex-1 text-left text-sm leading-tight">
                        <span className="truncate font-semibold">{user.nickname || user.email?.split("@")[0]}</span>
                        <span className="truncate text-xs">{user.email}</span>
                      </div>
                    </div>
                  </DropdownMenuLabel>
                  <DropdownMenuSeparator />
                  <DropdownMenuGroup>
                    <DropdownMenuItem>
                      <Coins className="mr-2 size-4 text-yellow-500" />
                      <span className="font-medium">{credits !== null ? credits : "..."} </span>
                    </DropdownMenuItem>
                    <DropdownMenuItem asChild>
                      <Link href="/pricing">
                        <Sparkles className="mr-2 size-4 text-purple-500" />
                        <span></span>
                      </Link>
                    </DropdownMenuItem>
                  </DropdownMenuGroup>
                  <DropdownMenuSeparator />
                  <DropdownMenuGroup>
                    <DropdownMenuItem asChild>
                      <Link href="/settings">
                        <Settings className="mr-2 size-4" />
                        <span></span>
                      </Link>
                    </DropdownMenuItem>
                  </DropdownMenuGroup>
                  <DropdownMenuSeparator />
                  <DropdownMenuItem onSelect={handleLogout} className="cursor-pointer text-red-500 focus:text-red-500">
                    <LogOut className="mr-2 size-4" />
                    <span></span>
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </SidebarMenuItem>
          ) : (
             <SidebarMenuItem>
                <SidebarMenuButton size="lg" className="animate-pulse">
                   <div className="h-8 w-8 rounded-lg bg-gray-200" />
                </SidebarMenuButton>
             </SidebarMenuItem>
          )}
        </SidebarMenu>
      </SidebarFooter>
      <SidebarRail />
    </>
  )
}

export function AppSidebar({ ...props }: React.ComponentProps<typeof Sidebar>) {
  return (
    <Sidebar collapsible="offcanvas" {...props}>
      <SidebarContent_ />
    </Sidebar>
  )
}